.about__containers
{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me
{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-light),transparent);
    
    display: grid;
    place-items: center;
}

.about__me-image{
    height: 120%;
    border-radius: 2rem;
    overflow:hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0deg);
}

.about__cards
{
     display: grid;
     grid-template-columns: repeat(3,1fr);
     gap: 1.5rem;
}

.mycard{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.mycard:hover{
    
        background: transparent;
        border-color:var(--color-bg-variant) ;
        cursor: default;
    }
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.mycard h5{
font-size: 0.9rem;
}
.mycard small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{

    margin: 2rem 0rem 2.6rem;
    color: var(--color-light);
}

@media screen and (max-width:1024px) {
    .about__containers{
        grid-template-columns: 1fr;
        gap:0;

    }
    .about__me{
        width: 50%;
        margin:2rem auto 4rem;

    }
    .about__content p {
    margin:1rem 0rem 1.5rem;
    };
    
    }

@media screen and (max-width:600px) {
    .about__me{
        width: 65%;
        margin:0rem auto 3rem;

    }
    .about__cards
{
     grid-template-columns: 1fr 1fr;
     gap: 1rem;
}
.about__content  {
    text-align: center;
    };
    .about__content p {
        margin: 1.5rem 0rem;
        };
}
